@import url('https://fonts.googleapis.com/css?family=Montserrat:200,400,700');
@import "sidebar";
@import "navbar";
$break-large: 768px;
body {
    font-family: 'Montserrat', sans-serif;
    overflow: hidden;
    background-color: #fafafa;
}

p {
    font-size: 1em;
    font-weight: 400;
    line-height: 1.5em;
    color: #999999;
}

a,
a:hover,
a:focus {
    color: inherit;
    text-decoration: none;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

.wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}

#content {
    margin-left: 260px;
    width: 100%;
    min-width: 480px;
    min-height: 100vh;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

#content.active {
    margin-left: 0px;
}

.content-body {
    padding: 20px;
}
#sidebarCollapse {
    width: 40px;
    height: 40px;
    background: #f5f5f5;
    cursor: pointer;
    span {
        width: 80%;
        height: 1px;
        margin: 0 auto;
        display: block;
        background: #ffffff;
        -webkit-transition: all 0.8s cubic-bezier(0.810, -0.330, 0.345, 1.375);
        transition: all 0.8s cubic-bezier(0.810, -0.330, 0.345, 1.375);
        -webkit-transition-delay: 0.2s;
        transition-delay: 0.2s;
    }
    span:first-of-type {
        -webkit-transform: rotate(45deg) translate(1px, 1px);
        transform: rotate(45deg) translate(1px, 1px);
    }
    span:nth-of-type(2) {
        opacity: 0;
    }
    span:last-of-type {
        -webkit-transform: rotate(-45deg) translate(1px, -1px);
        transform: rotate(-45deg) translate(1px, -1px);
    }
}

#sidebarCollapse.active span {
    -webkit-transform: none;
    transform: none;
    opacity: 1;
    margin: 5px auto;
}

.navbar-btn {
    -webkit-box-shadow: none;
    box-shadow: none;
    outline: none !important;
    border: none;
    background: none !important;
}

.navbar-brand {
    text-transform: uppercase;
    font-size: 0.9em;
    padding-left: 0.3rem;
}

.panel-header-background {
    background: #141e30;
    background: -webkit-gradient(linear, left top, right top, color-stop(0, #0c2646), color-stop(60%, #204065), to(#2a5788));
    background: linear-gradient(90deg, #0c2646 0, #204065 60%, #2a5788);
}
#sidebar[data-color="blue"] {
    background-color: #2ca8ff !important;
    ul {
        li.active>a {
            color: #2ca8ff !important;
            background-color: #ffffff;
        }
    }
}

#sidebar[data-color="orange"] {
    background-color: #f96332 !important;
    ul {
        li.active>a {
            color: #f96332 !important;
            background-color: #ffffff;
        }
    }
}

#sidebar {
    width: 260px;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 999;
    background-color: #2ca8ff;
    color: #fff;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    -webkit-box-shadow: 0 2px 22px 0 rgba(0, 0, 0, 0.2), 0 2px 30px 0 rgba(0, 0, 0, 0.35);
    -o-box-shadow: 0 2px 22px 0 rgba(0, 0, 0, 0.2), 0 2px 30px 0 rgba(0, 0, 0, 0.35);
    box-shadow: 0 2px 22px 0 rgba(0, 0, 0, 0.2), 0 2px 30px 0 rgba(0, 0, 0, 0.35);
    .sidebar-header {
        padding: 0.5rem 0.7rem;
        border-bottom: 1px solid rgba(255, 255, 255, 0.5);
        width: calc(100% - 30px);
        margin: 0 auto;
        a {
            font-size: 0.9em;
            font-weight: 400;
            text-transform: uppercase;
            line-height: 45px;
        }
    }
    ul.components {
        border-bottom: 1px solid rgba(255, 255, 255, 0.5);
        width: calc(100% - 30px);
        margin: 0 auto;
        margin-top: 0.5rem;
    }
    ul {
        li {
            a {
                text-transform: uppercase;
                padding: 10px;
                font-size: 0.7em;
                line-height: 25px;
                display: block;
                border-radius: 30px;
                margin-bottom: 5px;
            }
            a:hover {
                color: #ffffff;
                background-color: rgba(255, 255, 255, 0.1);
            }
        }
        p {
            color: #ffffff;
            font-size: 0.9em;
            padding: 0px 10px;
        }
        li.active>a {
            color: #2ca8ff;
            background-color: #ffffff;
        }
        a[aria-expanded="true"] {
            color: #ffffff;
            background-color: rgba(255, 255, 255, 0.1);
        }
        ul {
            a {
                font-size: 0.7em !important;
            }
        }
    }
}

#sidebar.active {
    margin-left: -260px;
    -webkit-box-shadow: none;
    -o-box-shadow: none;
    box-shadow: none;
}

a[data-toggle="collapse"] {
    position: relative;
}

.simple-text-user {
    text-transform: none !important;
    font-size: 14px !important;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-50%) rotate(0deg);
    transform: translateY(-50%) rotate(0deg);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

a[aria-expanded="true"]::after {
    -webkit-transform: translateY(-50%) rotate(180deg);
    transform: translateY(-50%) rotate(180deg);
}

.photo {
    float: left;
    width: 36px;
    height: 36px;
    margin-top: -5px;
    margin-right: 8px;
    -webkit-box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.3);
    -o-box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.3);
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
}